.recipe{
    border-radius: 10px;
    box-shadow : 0px 5px 20px rgb(71,71,71);
    margin : 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    max-width: fit-content;
}
.image{
    border-radius: 50%;
    width: 100px;
    height: 100px;
    margin-top: 10px;
}
.calories{
 background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
 color: black;
 font-size: 25px;
 font-weight: bolder;
 padding: 15px;
 border-radius: 10px;
 text-align: center;
}